/* ---------- NAVBAR ---------- */

.Navbar > div {
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    align-items: stretch;
    margin: 0 auto 10px;
    max-width: 800px;
    padding: 15px 0px;
    border: var(--drop-border);
    border-radius: 6px;
    background: var(--drop-background);
    opacity: 0.9;
    backdrop-filter: blur(10px);
    font-family: -apple-system, "Inter", sans-serif;
}
.Navbar > div > div { text-align: center; }
.Navbar > div a {
    display: inline-block;
    color: var(--slate-300-color);
    transition: var(--color-transition);
    font-size: 0.8rem;
    text-decoration: none;
}
.Navbar > div  a.active {
    color: var(--cool-blue-color);
    font-weight: 600;
}
.Navbar > div span, .Navbar > div svg {
    font-size: 20px;
    display: block;

    margin: auto;
    margin-bottom: 0.2rem;
}

/* ---------- ARTICLE PREVIEW ---------- */

.ArticlePreview {
	padding: 50px;
	border: var(--drop-border);
	border-radius: 10px;
	background: var(--drop-background);
}
.ArticlePreview > h1 {
	margin-bottom: 0;
	color: var(--cool-blue-color);
}
.ArticlePreview > p {
	margin: 0 0 15px 0;
	font-size: 0.9em;
	font-style: italic;
}
.ArticlePreview > h4 { 
	font-weight: 500;
	line-height: 1.6rem; 
}

/* ---------- BACK BUTTON ---------- */

.BackButton, .LikesSection {
	display: grid;
	grid-template-rows: 1fr;
	grid-auto-columns: 1fr;
	align-items: center;
	justify-items: center;
	margin: 0 0 0 auto;
	background: rgba(14, 20, 31, 0.9);
	width:  68px;
	height: 68px;
	border-radius: 34px;
	box-shadow: 0 0 8px rgba(0, 0, 0, 0.6);
	padding: 0;
	-webkit-transition: box-shadow 0.2s ease-out;
	transition: box-shadow 0.2s ease-out;
}
.BackButton {
	position: fixed;
	bottom: 10px;
	left: 10px;
	z-index: 3;
}
.BackButton:active, .LikesSection:active  { box-shadow: 0 0 0 rgba(0, 0, 0, 0); }

/* ---------- LIKES SECTION ---------- */

.LikesSection {
	position: relative;
	min-width: 68px;
}
.LikesSection > p {
	align-self: center;
	margin: 0;
}
.LikesSection > span {
	position: absolute;
	top: -8px;
	right: 8px;
	background-color: #0098BF;
	border-radius: 5px;
	padding: 2px 5px;
}

/* ---------- RELATED ARTICLES ---------- */

.RelatedArticles > ul {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    grid-gap: 10px;
    list-style: none;
    height: 70%;
    padding: 0;

    grid-template-areas: 
        "first second third";
}
.RelatedArticles > ul > li:nth-child(1) { grid-area: first; }
.RelatedArticles > ul > li:nth-child(2) { grid-area: second; }
.RelatedArticles > ul > li:nth-child(3) { grid-area: third; }
.RelatedArticles > ul > li {
    background-color: var(--drop-background);
    border-radius: 10px;
    border: var(--drop-border);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.8);
    padding: 10px;
}
.RelatedArticles > ul > li > a {
    text-decoration: none;
    font-weight: bold;
}
.RelatedArticles > ul > li > a > span {
    font-size: 0.7em;
}
.RelatedArticles > ul > li > a > h4 {
    margin: 10px 0;
}

/* ---------- TAG ---------- */

.Tag {
	margin: 5px;
	padding: 3px 10px;
	background-color: var(--slate-500-color);
	opacity: 0.8;
	box-shadow: 0 0 5px rgba(0, 0, 0, 0.6);
	border-radius: 3px;
	font-weight: bold;
}
.Tag--html {
	background-color: #E44D26;
	color: #FFFFFF;
}
.Tag--css {
	background-color: #1572B6;
	color: #FFFFFF;
}
.Tag--golang {
	background-color: #2EDAE8;
	color: #FFFFFF;
}
.Tag--swift {
	background-color: #F05138;
	color: #FFFFFF;
}
.Tag--ios {
	background-color: #111111;
	color: #FFFFFF;
}
.Tag--uikit {
	background-color: #9E9E9E;
	color: #3F3F3F;
}
.Tag--java {
	background-color: #332C2A;
	color: #F86600;
}
.Tag--spring {
	background-color: #52B622;
	color: #1A1F23;
}
.Tag--thymeleaf {
	background-color: #ECECEC;
	color: #006100;
}
.Tag--react {
	background-color: #00DDFF;
	color: #0F1622;
}
.Tag--redux {
	background-color: #764ABC;
}
.Tag--node {
	background-color: #168934;
	color: #FFFFFF;
}
.Tag--javascript {
	background-color: #FFDB00;
	color: #000000;
}
.Tag--career {
	background-color: #1F6ED7;
	color: #FFFFFF;
}

/* ---------- SHARED ---------- */

svg {
    width: 24px;
    height: 24px;
}

/* ---------- MEDIA QUERIES ---------- */

@media (max-width: 825px) {
	/* ---------- NAVBAR ---------- */
    .Navbar > div { margin: 0 10px 10px; }

    /* ---------- BACK BUTTON ---------- */

    .BackButton {
		top: 11px;
		left: 10px;
		right: initial;
		width:  48px;
		height: 48px;
		border-radius: 24px;
		background-color: #0098BF;
		box-shadow: initial;
	}
	.BackButton > svg { color: #CCCCCC; }
}

@media (min-width: 826px) {
	/* ---------- BACK BUTTON ---------- */
	.BackButton { display: none;}
}

@media (max-width: 800px) {
	/* ---------- NAVBAR ---------- */
    .Navbar {
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        z-index: 3;
    }

    /* ---------- RELATED ARTICLES ---------- */
    .RelatedArticles > ul {
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr 1fr;
        grid-template-areas: 
            "first second"
            "third third";
    }
}


@media (max-width: 500px) {
	/* ---------- RELATED ARTICLES ---------- */
    .RelatedArticles > ul {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr 1fr;
        grid-template-areas: 
            "first"
            "second"
            "third";
    }
}