:root {
    --drop-background: #0F1622;
    --drop-border-color: #0B1019;
    --drop-border: 3px solid var(--drop-border-color);
    --color-transition: color 0.5s;

    --cool-blue-color: #0098Bf;
    --strawberry-100-color: #ff8c82;
    --strawberry-300-color: #ed5353;
    --strawberry-500-color: #c6262e;
    --strawberry-700-color: #a10705;
    --strawberry-900-color: #7a0000;
    --orange-100-color: #ffc27d;
    --orange-300-color: #ffa154;
    --orange-500-color: #f37329;
    --orange-700-color: #cc3b02;
    --orange-900-color: #a62100;
    --banana-100-color: #fff394;
    --banana-300-color: #ffe16b;
    --banana-500-color: #f9c440;
    --banana-700-color: #d48e15;
    --banana-900-color: #ad5f00;
    --lime-100-color: #d1ff82;
    --lime-300-color: #9bdb4d;
    --lime-500-color: #68b723;
    --lime-700-color: #3a9104;
    --lime-900-color: #206b00;
    --mint-100-color: #89ffdd;
    --mint-300-color: #43d6b5;
    --mint-500-color: #28bca3;
    --mint-700-color: #0e9a83;
    --mint-900-color: #007367;
    --blueberry-100-color: #8cd5ff;
    --blueberry-300-color: #64baff;
    --blueberry-500-color: #3689e6;
    --blueberry-700-color: #0d52bf;
    --blueberry-900-color: #002e99;
    --grape-100-color: #e4c6fa;
    --grape-300-color: #cd9ef7;
    --grape-500-color: #a56de2;
    --grape-700-color: #7239b3;
    --grape-900-color: #452981;
    --bubblegum-100-color: #fe9ab8;
    --bubblegum-300-color: #f4679d;
    --bubblegum-500-color: #de3e80;
    --bubblegum-700-color: #bc245d;
    --bubblegum-900-color: #910e38;
    --cocoa-100-color: #a3907c;
    --cocoa-300-color: #8a715e;
    --cocoa-500-color: #715344;
    --cocoa-700-color: #57392d;
    --cocoa-900-color: #3d211b;
    --silver-100-color: #fafafa;
    --silver-300-color: #d4d4d4;
    --silver-500-color: #abacae;
    --silver-700-color: #7e8087;
    --silver-900-color: #555761;
    --slate-100-color: #95a3ab;
    --slate-300-color: #667885;
    --slate-500-color: #485a6c;
    --slate-700-color: #273445;
    --slate-900-color: #0e141f;
    --dark-100-color: #666;
    --dark-300-color: #4d4d4d;
    --dark-500-color: #333;
    --dark-700-color: #1a1a1a;
    --dark-900-color: #000;
}

* { box-sizing: border-box; }

#root {
    width: 100% !important;
    height: 100% !important;
    padding: 0 !important;
    margin: 0 !important;
}

body {
    font-family: 'Recursive', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
h1, h2, h3, h4, h5, h6, p, span { color: #d4d4d4; color: var(--silver-300-color); }
code { font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }
a, a:hover { text-decoration: none; }
hr { 
    border: none;
    height: 1.5px;
    background-color: #485a6c;
    background-color: var(--slate-500-color); 
}

.underlined-text { border-bottom: 2px solid #0098Bf; border-bottom: 2px solid var(--cool-blue-color); }
.shadow { box-shadow: 0 6px 2px -2px rgba(0, 0, 0, 0.3); }
.cool-color { color: #0098Bf; color: var(--cool-blue-color); }
.cool-link {
    color: #485a6c;
    color: var(--slate-500-color);
    transition: color 0.5s;
    transition: var(--color-transition);
    justify-content: center;
}
.cool-link:hover { color: #0098Bf; color: var(--cool-blue-color); }
.to-shadow {
    transition-timing-function: ease;
    transition: box-shadow 0.5s;
}
.to-shadow:hover { box-shadow: 0 10px 18px rgba(0, 0, 0, 0.7); }
.tags {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 0 -5px;
}
.drop {
    display: flex;
    flex-direction: row;
    align-items: center;
    border: 3px solid #0B1019;
    border: var(--drop-border);
    border-radius: 6px;
    background-color: #0F1622;
    background-color: var(--drop-background);
    opacity: 0.9;
    padding: 15px;
    margin-top: 25px;
}
.drop:first-child {margin-top: 0; }


.App { margin: 162px 0 0 0; }
.App > header {	
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	width: 100%;
	background-color: #202b38;
	z-index: 3;
}
.App > header.shadow { box-shadow: 0 4px 2px -2px rgba(0, 0, 0, 0.3); }
.App > header > h1 {
	white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
	padding: 10px 0;
	margin: 0 auto; 
	max-width: 800px;
}

/* ---------- MEDIA QUERIES ---------- */

@media (max-width: 800px) {
	.App { margin: 72px 0 105px 0; }
}

@media (max-width: 825px) {
	.App > header > h1 { padding-left: 10px; }
	.App > header > h1.article-title { padding-left: 68px; }
}
/* ---------- NAVBAR ---------- */

.Navbar > div {
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    align-items: stretch;
    margin: 0 auto 10px;
    max-width: 800px;
    padding: 15px 0px;
    border: var(--drop-border);
    border-radius: 6px;
    background: var(--drop-background);
    opacity: 0.9;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    font-family: -apple-system, "Inter", sans-serif;
}
.Navbar > div > div { text-align: center; }
.Navbar > div a {
    display: inline-block;
    color: var(--slate-300-color);
    transition: var(--color-transition);
    font-size: 0.8rem;
    text-decoration: none;
}
.Navbar > div  a.active {
    color: var(--cool-blue-color);
    font-weight: 600;
}
.Navbar > div span, .Navbar > div svg {
    font-size: 20px;
    display: block;

    margin: auto;
    margin-bottom: 0.2rem;
}

/* ---------- ARTICLE PREVIEW ---------- */

.ArticlePreview {
	padding: 50px;
	border: var(--drop-border);
	border-radius: 10px;
	background: var(--drop-background);
}
.ArticlePreview > h1 {
	margin-bottom: 0;
	color: var(--cool-blue-color);
}
.ArticlePreview > p {
	margin: 0 0 15px 0;
	font-size: 0.9em;
	font-style: italic;
}
.ArticlePreview > h4 { 
	font-weight: 500;
	line-height: 1.6rem; 
}

/* ---------- BACK BUTTON ---------- */

.BackButton, .LikesSection {
	display: grid;
	grid-template-rows: 1fr;
	grid-auto-columns: 1fr;
	align-items: center;
	justify-items: center;
	margin: 0 0 0 auto;
	background: rgba(14, 20, 31, 0.9);
	width:  68px;
	height: 68px;
	border-radius: 34px;
	box-shadow: 0 0 8px rgba(0, 0, 0, 0.6);
	padding: 0;
	transition: box-shadow 0.2s ease-out;
}
.BackButton {
	position: fixed;
	bottom: 10px;
	left: 10px;
	z-index: 3;
}
.BackButton:active, .LikesSection:active  { box-shadow: 0 0 0 rgba(0, 0, 0, 0); }

/* ---------- LIKES SECTION ---------- */

.LikesSection {
	position: relative;
	min-width: 68px;
}
.LikesSection > p {
	align-self: center;
	margin: 0;
}
.LikesSection > span {
	position: absolute;
	top: -8px;
	right: 8px;
	background-color: #0098BF;
	border-radius: 5px;
	padding: 2px 5px;
}

/* ---------- RELATED ARTICLES ---------- */

.RelatedArticles > ul {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    grid-gap: 10px;
    list-style: none;
    height: 70%;
    padding: 0;

    grid-template-areas: 
        "first second third";
}
.RelatedArticles > ul > li:nth-child(1) { grid-area: first; }
.RelatedArticles > ul > li:nth-child(2) { grid-area: second; }
.RelatedArticles > ul > li:nth-child(3) { grid-area: third; }
.RelatedArticles > ul > li {
    background-color: var(--drop-background);
    border-radius: 10px;
    border: var(--drop-border);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.8);
    padding: 10px;
}
.RelatedArticles > ul > li > a {
    text-decoration: none;
    font-weight: bold;
}
.RelatedArticles > ul > li > a > span {
    font-size: 0.7em;
}
.RelatedArticles > ul > li > a > h4 {
    margin: 10px 0;
}

/* ---------- TAG ---------- */

.Tag {
	margin: 5px;
	padding: 3px 10px;
	background-color: var(--slate-500-color);
	opacity: 0.8;
	box-shadow: 0 0 5px rgba(0, 0, 0, 0.6);
	border-radius: 3px;
	font-weight: bold;
}
.Tag--html {
	background-color: #E44D26;
	color: #FFFFFF;
}
.Tag--css {
	background-color: #1572B6;
	color: #FFFFFF;
}
.Tag--golang {
	background-color: #2EDAE8;
	color: #FFFFFF;
}
.Tag--swift {
	background-color: #F05138;
	color: #FFFFFF;
}
.Tag--ios {
	background-color: #111111;
	color: #FFFFFF;
}
.Tag--uikit {
	background-color: #9E9E9E;
	color: #3F3F3F;
}
.Tag--java {
	background-color: #332C2A;
	color: #F86600;
}
.Tag--spring {
	background-color: #52B622;
	color: #1A1F23;
}
.Tag--thymeleaf {
	background-color: #ECECEC;
	color: #006100;
}
.Tag--react {
	background-color: #00DDFF;
	color: #0F1622;
}
.Tag--redux {
	background-color: #764ABC;
}
.Tag--node {
	background-color: #168934;
	color: #FFFFFF;
}
.Tag--javascript {
	background-color: #FFDB00;
	color: #000000;
}
.Tag--career {
	background-color: #1F6ED7;
	color: #FFFFFF;
}

/* ---------- SHARED ---------- */

svg {
    width: 24px;
    height: 24px;
}

/* ---------- MEDIA QUERIES ---------- */

@media (max-width: 825px) {
	/* ---------- NAVBAR ---------- */
    .Navbar > div { margin: 0 10px 10px; }

    /* ---------- BACK BUTTON ---------- */

    .BackButton {
		top: 11px;
		left: 10px;
		right: auto;
		right: initial;
		width:  48px;
		height: 48px;
		border-radius: 24px;
		background-color: #0098BF;
		box-shadow: none;
		box-shadow: initial;
	}
	.BackButton > svg { color: #CCCCCC; }
}

@media (min-width: 826px) {
	/* ---------- BACK BUTTON ---------- */
	.BackButton { display: none;}
}

@media (max-width: 800px) {
	/* ---------- NAVBAR ---------- */
    .Navbar {
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        z-index: 3;
    }

    /* ---------- RELATED ARTICLES ---------- */
    .RelatedArticles > ul {
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr 1fr;
        grid-template-areas: 
            "first second"
            "third third";
    }
}


@media (max-width: 500px) {
	/* ---------- RELATED ARTICLES ---------- */
    .RelatedArticles > ul {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr 1fr;
        grid-template-areas: 
            "first"
            "second"
            "third";
    }
}
/* ---------- ARTICLE LIST PAGE ---------- */

.ArticleListPage { padding-top: 20px; }
.ArticleListPage > ul {
	padding: 0;
	list-style: none;
}
.ArticleListPage > ul > li { margin-bottom: 30px; }
.ArticleListPage > ul > li:last-child { margin-bottom: 0; }

/* ---------- ABOUT PAGE ---------- */

.AboutPage {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 25px;
}
.AboutPage > img {
	min-width: 250px;
	border: 6px solid var(--slate-500-color);
	border-radius: 125px;
	background-color: var(--drop-background);
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.8);
}
.AboutPage > p { font-size: 1.4rem; } 
.AboutPage__Footer { margin-top: 25px; }
.AboutPage__Footer > div:first-child > ul {
	display: inline-block;
	padding: 0;
	margin: 0 0 0 15px;
	list-style: none;
}
.AboutPage__Footer > div:first-child > ul > li {
	display: inline-block;
	margin-right: 15px;
}
.AboutPage__Footer > div:first-child > ul > li:last-child { margin-right: 0; }

/* ---------- ARTICLE PAGE ---------- */

.ArticlePage > article > div:first-child {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin-top: 20px;
}
.ArticlePage > article > div:first-child > div > h4 { font-size: 1.2rem; }
.ArticlePage > article > div:first-child > div > p { font-style: italic; }
.ArticlePage > article > div:last-child > p { line-height: 1.6rem; }

/* ---------- SKILLS PAGE ---------- */

.SkillsPage > div {
	padding: 15px -5px;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-items: center;
	/*margin: 0 -20px;*/
}
.SkillsPage > div > div {
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	align-items: center;
	margin: 20px;
}
.SkillsPage > div > div > svg {
	width: 75px;
	height: 75px;
}
.SkillsPage > div > div > span {
	font-size: 0.9rem;
	margin-top: 10px;
}

/* ---------- WORK PAGE ---------- */

.WorkPage > ul, 
.ExperiencePage > ul {
    list-style: none;
    padding: 15px;
}
.WorkPage > ul > a, 
.ExperiencePage > ul > li {
	display: block;
	margin-bottom: 25px;
	padding: 25px;
	background: var(--drop-background);
	border: 3px solid var(--drop-border-color);
	border-radius: 10px;
}

.WorkPage > ul > a > li {
	display: flex;
	flex-direction: row;
	align-items: center;
	align-content: center;
}
.WorkPage > ul > a > li > .content {
	width: 66%;
	margin-right: 1%;
}
.WorkPage > ul > a > li > .thumbnail {
	width: 33%;
}

.WorkPage > ul > a > li > .thumbnail > img {
	border-radius: 15px;
}
.WorkPage > ul > a > li > .content > h4 {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
}
.WorkPage > ul > a > li > .content > h4 > span { margin-right: 10px; }
.WorkPage > ul > a > li > .content > h4 > img {
    border-radius: 10px;
    margin: 0 0 0 auto;
}

/* ---------- EXPERIENCE PAGE ---------- */
.ExperiencePage > ul > li > .content > h3 { margin-top: 0; } 
.ExperiencePage > ul > li > .content > p { font-style: italic; }
.ExperiencePage > ul > li > .content > ul {
	padding-left: 20px;
	margin-top: 10px;
}

/* ---------- SHARED ---------- */

.ArticleListPage, .ArticlePage, .AboutPage, .SkillsPage, .WorkPage, .ExperiencePage  {
    width: 100%;
    height: 100%;
    position: relative;
}

.ArticleListPage > ul, 
.WorkPage > ul, 
.ExperiencePage > ul, 
.WorkPage > ul > a:last-child, 
.ExperiencePage > ul > li:last-child,
.ExperiencePage > ul > li > h4 { margin: 0; }

.ArticlePage > article > div:first-child > div > h4,
.ArticlePage > article > div:first-child > div > p,
.WorkPage > ul > a > li > .content h4,
.ExperiencePage > ul > li > p { margin: 0 0 10px 0; }

.ExperiencePage > ul:nth-child(2),
.WorkPage > ul:nth-child(2) { position: relative; }
.ExperiencePage > ul:nth-child(2) > li::after,
.WorkPage > ul:nth-child(2) > a::after  { 
	position: absolute;
    top: 10px; bottom: 10px;
    left: 10px; right: 10px;
    background: linear-gradient(45deg, var(--cool-blue-color), var(--slate-500-color));
    content: '';
    z-index: -1;
    border-radius: 15px;
}
